<template>
  <div>
    <div class="content-section introduction">
      <div class="feature-intro">
        <h1>TreeTable <span>Templating</span></h1>
        <p>Custom content at header, body and footer sections are supported via templating.</p>
      </div>
    </div>

    <div class="content-section implementation">
      <div class="card">
        <TreeTable :value="nodes">
          <template #header>
            FileSystem
          </template>
          <Column field="name" header="Name" :expander="true"></Column>
          <Column field="size" header="Size"></Column>
          <Column field="type" header="Type"></Column>
          <Column :headerStyle="{ 'width': '10rem' }" headerClass="text-center" bodyClass="text-center">
            <template #header>
              <Button type="button" icon="pi pi-cog"></Button>
            </template>
            <template #body>
              <Button type="button" icon="pi pi-search" class="p-button-success" style="margin-right: .5em"></Button>
              <Button type="button" icon="pi pi-pencil" class="p-button-warning"></Button>
            </template>
          </Column>
          <template #footer>
            <div style="text-align:left">
              <Button icon="pi pi-refresh" />
            </div>
          </template>
        </TreeTable>
      </div>
    </div>

    <TreeTableTemplatingDoc />
  </div>
</template>

<script>
import TreeTableTemplatingDoc from '@/doc/treetable/Templating.vue'
import NodeService from '../../service/NodeService'

export default {
  data() {
    return {
      nodes: null
    }
  },
  nodeService: null,
  created() {
    this.nodeService = new NodeService()
  },
  mounted() {
    this.nodeService.getTreeTableNodes().then(data => this.nodes = data)
  },
  components: { TreeTableTemplatingDoc }
}
</script>
